.header-box {
    // background-color: indianred;
    position: relative;
    z-index: 5;
    .header-box__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
    }
}

  ///////////////
  .logo {
    width: 5em;
    height: 5em;
    animation: bounceIn 0.6s;
    transform: rotate(0deg) scale(1) translateZ(0);
    transition: all 0.4s cubic-bezier(.8,1.8,.75,.75);
    cursor: pointer;
  }
  
  .logo:hover {
    transform: rotate(10deg) scale(1.1);
  }
  
  .bubble { fill: #e76853; }
  .icon { fill:  #fff; }
  
  @keyframes bounceIn {
    0% {
      opacity: 1;
      transform: scale(.3);
    } 
  
    50% {
      opacity: 1;
      transform: scale(1.05);
    } 
  
    70% {
      opacity: 1;
      transform: scale(.9);
    }
  
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }